// src/app/page.js
import Navbar from '@/components/home/Navbar'
import SubNavbar from '@/components/home/SubNavbar'
import Products from '@/components/home/Products' // Yangilangan komponent
import productsData from '@/data/Products.json' // Barcha ma'lumotlar shu yerdan olinadi

export default function Home({ searchParams }) {
	// 1. URL parametrlarini olish (Next.js server component feature)
	const action = searchParams?.action // 'catalog' yoki 'search'
	const value = searchParams?.value // masalan 'Aqlli uy'

	let displayProducts = productsData
	let pageTitle = 'Mashhur Mahsulotlar'

	// 2. FILTRLASH MANTIQI
	if (action === 'catalog' && value) {
		// Katalogni filtrlash (Products.json dagi 'catalog' fieldi bo'yicha)
		displayProducts = productsData.filter(p => p.catalog === value)
		pageTitle = `${value}`
	} else if (action === 'search' && value) {
		// Qidiruv
		const lowerValue = value.toLowerCase()
		displayProducts = productsData.filter(
			p =>
				p.nomi.toLowerCase().includes(lowerValue) ||
				(p.brand && p.brand.toLowerCase().includes(lowerValue)) ||
				(p.category && p.category.toLowerCase().includes(lowerValue))
		)
		pageTitle = `"${value}" bo'yicha qidiruv natijalari`
	}

	// 3. SUBNAVBARNI BOSHQARISH
	// Agar action (search yoki catalog) bor bo'lsa, SubNavbar ko'rinmasin
	const showSubNavbar = !action

	return (
		<main className='min-h-screen bg-white'>
			<Navbar />

			{/* SubNavbar faqat Bosh sahifada (filtrsiz) ko'rinadi */}
			{showSubNavbar && <SubNavbar />}

			{/* Sarlavha va Natijalar soni */}
			<div className='container mx-auto px-4 mt-8 mb-4'>
				<div className='flex items-center justify-between'>
					<h2 className='text-2xl font-bold text-gray-800'>{pageTitle}</h2>
					{action && (
						<span className='text-gray-500 text-sm bg-gray-100 px-3 py-1 rounded-full'>
							{displayProducts.length} ta mahsulot
						</span>
					)}
				</div>
			</div>

			{/* 4. PRODUCTS KOMPONENTIGA FILTRLANGAN MA'LUMOTNI BERISH */}
			<Products products={displayProducts} />
		</main>
	)
}
